export const INCORRECT_ANSWERS = 5;
export const CANVAS_SIZE = 300;
export const SUCCESS_THRESHOLD = 0.6;
export const SOUND_EFFECT_VOLUME = 0.4;

export enum GuessResult {
  Successful,
  Passed,
  Failed,
}

export enum PlayerStatus {
  Loading,
  WaitingForPlayers,
  Started,
  RoundStarted,
  TurnStarted,
  GuessingStarted,
  TurnEnded,
  RoundEnded,
  Ended,
  Error,
}

export enum ControllerStatus {
  WaitingForPin,
  WaitingForName,
  WaitingForGameToStart,
  GameStarted,
  RoundStarted,
  TurnStarted,
  GuessingStarted,
  TurnEnded,
  RoundEnded,
  GameEnded,
  Error,
}

export interface TeamAssignment {
  cid: string;
  team: number;
  instructorOrder: number;
}

export namespace Team {
  export const Blue = 0;
  export const Red = 1;
}

export namespace RoundId {
  export const Round1 = 1;
  export const Round2 = 2;
  export const Round3 = 3;
}

export const FINAL_ROUND = RoundId.Round2;

export namespace Timing {
  export const GameStarted = 5000;
  export const RoundStarted = 5000;
  export const TurnStarted = 5000;
  export const GuessingSession = 30000;
  export const TeamResultsShown = 5000;
  export const PreviousGuessedWord = 4000;
  export const SlowPlayerWait = 5000;
}

export interface PreviousGuess {
  word: string | undefined;
  result: GuessResult;
}

export interface Round {
  id: number;
}

export interface Turn {
  team: number;
  instructor?: User;
  guessedWords: string[];
}

export interface User {
  cid: string;
  name: string;
}

export interface Guess {
  drawing: string;
  correctAnswers: number;
  incorrectAnswers: number;
  correctWord: string;
  incorrectWords: string[];
}

