import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { selectPlayerTheme, startNewRound } from "../playerSlice";

export const PlayerStarted = () => {
  const d = useAppDispatch();
  // useEffect(() => {
  //   setTimeout(() => {
  //     d(startNewRound());
  //   }, Timing.GameStarted);
  // });
  const theme = useAppSelector(selectPlayerTheme);

  return (
    <div className='fullscreen centered'>
      <h1>Theme: {theme}</h1>
      <div className='instructions'>Check on your phone to know your team</div>
      <div className='versus'>
        <div className='versus__team'>Blue team</div><div className='versus__versus'>vs</div><div className='versus__team'>Orange team</div>
      </div>
      <p><button onClick={() => d(startNewRound())}>Next</button></p>
    </div>
  );
};
