import { useEffect } from "react";
import { useAppSelector } from "../../../app/hooks";
import { playSound } from "../../../app/utils";
import {
  selectPlayerRound, selectPlayerScores,
} from "../playerSlice";
import { Versus } from "./Versus";

export const PlayerEnded = () => {
  const round = useAppSelector(selectPlayerRound);
  const scores = useAppSelector(selectPlayerScores);
  const winner = scores[0] > scores[1] ? 0 : scores[0] < scores[1] ? 1 : null;  
  useEffect(() => {
    playSound('podiumSound', 0.2);
  }, []);
  return (
    <div className="fullscreen centered">
      <div className='instructions'>Round {round?.id}</div>
      <h1>
        {winner === null && <div>It is a TIE!</div>}
        {winner === 0 && <div>BLUE team wins!</div>}
        {winner === 1 && <div>ORANGE team wins!</div>}
      </h1>
      <Versus scores={scores} />
    </div>
  );
};
