import clsx from "clsx";
import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { shuffle } from "../../../app/utils";
import { CANVAS_SIZE, GuessResult, RoundId, Team } from "../../../constants";
import {
  selectControllerClientId,
  selectControllerGuess,
  selectControllerPreviousGuess,
  selectControllerRound,
  selectControllerTeamAssignments,
  selectControllerTurn,
  skipCurrentGuess,
  submitVote,
  updateDrawing,
} from "../controllerSlice";
import CanvasDraw from "react-canvas-draw";
import LZString from "lz-string";
import { ColorButton } from "./ColorButton";

const VoteButton = ({
  color,
  disabled,
  label,
  selected,
  onClick,
}: {
  color: string;
  disabled: boolean;
  label: string;
  selected: boolean;
  onClick: (value: string) => void;
}) => {
  return (
    <button
      disabled={disabled}
      className={clsx("button--answer", color, { selected })}
      onClick={() => {
        onClick(label);
      }}
    >
      {label}
    </button>
  );
};

export const ControllerGuessingStarted = () => {
  const [userVote, setUserVote] = useState("");
  const round = useAppSelector(selectControllerRound);
  const turn = useAppSelector(selectControllerTurn);
  const clientId = useAppSelector(selectControllerClientId);
  const myTeam = useAppSelector(selectControllerTeamAssignments)?.find(
    (x) => x.cid === clientId
  )?.team;
  const guess = useAppSelector(selectControllerGuess);
  const previousGuess = useAppSelector(selectControllerPreviousGuess);
  const words = useMemo(() => {
    if (!guess || !guess?.incorrectWords || !guess?.correctWord) {
      return [];
    }
    return shuffle([...guess?.incorrectWords, guess?.correctWord]);
  }, [guess?.correctWord]);

  const drawingRef: any = useRef(null);
  const [userCanInput, setUserCanInput] = useState(true);
  const firstTimeRef = useRef(true);
  useEffect(() => {
    setUserVote("");
    if (drawingRef.current) {
      drawingRef.current.clear();
    }
    if (firstTimeRef.current) {
      firstTimeRef.current = false;
    } else {
      setUserCanInput(false);
      const timeout = setTimeout(() => {
        setUserCanInput(true);
      }, 2000);
      return () => {
        clearTimeout(timeout);
      };
    }
    return undefined;
  }, [guess?.correctWord]);

  const d = useAppDispatch();
  const onVote = (word: string) => {
    setUserVote(word);
    d(submitVote(word));
  };
  const shareDrawing = useCallback(
    (component: any) => {
      console.log("data", component.getSaveData());
      d(updateDrawing(LZString.compress(component.getSaveData())));
    },
    [d]
  );

  const [brushColor, setBrushColor] = useState("#000");
  return (
    <div className="fullscreen centered">
      {clientId === turn?.instructor?.cid && userCanInput && (
        <>
          <div>
            <button
              className="button--big red"
              onClick={() => d(skipCurrentGuess())}
            >
              Pass
            </button>
          </div>
          <div className="centered" style={{ flex: 1 }}>
            <div className="instructions">Make your team guess</div>
            <h1>{guess?.correctWord}</h1>
            {clientId === turn?.instructor?.cid &&
              round?.id === RoundId.Round3 && (
                <div style={{ margin: "1rem 0" }}>
                  <div className="color-button-list">
                    <ColorButton
                      onClick={setBrushColor}
                      color="#000"
                      selected={brushColor === "#000"}
                    />{" "}
                    <ColorButton
                      onClick={setBrushColor}
                      color="#E21B3C"
                      selected={brushColor === "#E21B3C"}
                    />
                  </div>
                  <CanvasDraw
                    ref={drawingRef}
                    onChange={shareDrawing}
                    brushRadius={4}
                    brushColor={brushColor}
                    canvasWidth={CANVAS_SIZE}
                    canvasHeight={CANVAS_SIZE}
                  />
                </div>
              )}
          </div>
        </>
      )}
      {turn?.team === myTeam && !userCanInput && (
        <>
          <div
            className={clsx(
              "fullscreen centered",
              previousGuess?.result === GuessResult.Successful ? "green" : "red"
            )}
          >
            {previousGuess?.result === GuessResult.Failed && <h1>INCORRECT</h1>}
            {previousGuess?.result === GuessResult.Passed && <h1>PASSED</h1>}
            {previousGuess?.result === GuessResult.Successful && (
              <h1>{previousGuess.word} was GUESSED!</h1>
            )}

            <div className="instructions">New word to guess!</div>
          </div>
        </>
      )}
      {clientId !== turn?.instructor?.cid &&
        turn?.team === myTeam &&
        userCanInput && (
          <>
            {!!userVote && (
              <div className="fullscreen centered">
                <h1>You selected {userVote}.</h1>
                <div className='instructions'>Waiting for others to
                answer.</div>
              </div>
            )}

            {!userVote && (
              <>
                <div className="centered" style={{ flex: 1 }}>
                  <h1>Choose the right answer</h1>
                </div>
                <div className="answer-list">
                  <VoteButton
                    color="blue"
                    label={words[0]}
                    onClick={onVote}
                    selected={userVote === words[0]}
                    disabled={!!userVote}
                  />
                  <VoteButton
                    color="blue"
                    label={words[1]}
                    onClick={onVote}
                    selected={userVote === words[1]}
                    disabled={!!userVote}
                  />
                  <VoteButton
                    color="blue"
                    label={words[2]}
                    onClick={onVote}
                    selected={userVote === words[2]}
                    disabled={!!userVote}
                  />
                  <VoteButton
                    color="blue"
                    label={words[3]}
                    onClick={onVote}
                    selected={userVote === words[3]}
                    disabled={!!userVote}
                  />
                  <VoteButton
                    color="blue"
                    label={words[4]}
                    onClick={onVote}
                    selected={userVote === words[4]}
                    disabled={!!userVote}
                  />
                  <VoteButton
                    color="blue"
                    label={words[5]}
                    onClick={onVote}
                    selected={userVote === words[5]}
                    disabled={!!userVote}
                  />
                </div>
              </>
            )}
          </>
        )}
      {turn?.team !== myTeam && <h1>The other team is playing</h1>}
    </div>
  );
};
