import { CometD } from "cometd";
import { Middleware } from "redux";
import {
  initializeAsync,
  setPlayerError,
  setIsLoaded,
  sendMessageFromPlayer,
  userJoined,
  startGame,
  startNewRound,
  startNewTurn,
  startGuessing,
  onSkipCurrentGuess,
  onVoteSubmitted,
  goToNextTurn,
  endTurn,
  checkIfEnoughAnswers,
} from "./playerSlice";
import { RootState } from "../../app/store";
import { AnyAction, Dispatch, MiddlewareAPI } from "@reduxjs/toolkit";
import {
  onGameStarted,
  onGuessingStarted,
  onTurnStarted,
  onTurnEnded,
  onGameEnded,
  onRoundEnded,
  onRoundStarted,
} from "../controller/controllerSlice";
import { PlayerStatus, PREMADE_WORDS, SOUND_EFFECT_VOLUME } from "../../constants";
import { playSound, shuffle } from "../../app/utils";

const cometd = new CometD();

function sendMessageToControllers(
  store: MiddlewareAPI<Dispatch<AnyAction>, any>,
  message: any
) {
  const state = store.getState() as RootState;
  const pin = state.player.pin;

  cometd.publish("/service/player", {
    gameid: pin,
    type: "message",
    host: "kahoot-stage.it",
    id: 0,
    content: JSON.stringify(message),
  });
}

function getQueryString(key: string) {
  const params = new URLSearchParams(window.location.search);
  return params.get(key);
}

export const playerSocketMiddleware: Middleware =
  (store) => (next) => (action) => {
    if (goToNextTurn.match(action)) {
      const result = next(action);
      const state = store.getState() as RootState;
      if (state.player.turn) {
        sendMessageToControllers(store, onTurnStarted(state.player.turn));
      }
      return result;
    }

    if (endTurn.match(action)) {
      sendMessageToControllers(store, onTurnEnded());
      return next(action);
    }

    if (checkIfEnoughAnswers.match(action)) {
      const state = store.getState() as RootState;
      const prevGuess = state.player.guess;
      
      const result = next(action);
      const stateAfter = store.getState() as RootState;

      if (stateAfter.player.status === PlayerStatus.Ended) {
        sendMessageToControllers(store, onGameEnded());
        return result;
      }
      if (stateAfter.player.status === PlayerStatus.RoundEnded) {
        sendMessageToControllers(store, onRoundEnded());
        return result;
      }
      if (stateAfter.player.guess && prevGuess !== stateAfter.player.guess) {
        sendMessageToControllers(
          store,
          onGuessingStarted({ guess: stateAfter.player.guess, previousGuess: stateAfter.player.previousGuess })
        );
      }
    }
    if (onVoteSubmitted.match(action)) {
      const state = store.getState() as RootState;
      const prevGuess = state.player.guess;
      const result = next(action);
      const stateAfter = store.getState() as RootState;

      if (stateAfter.player.status === PlayerStatus.Ended) {
        sendMessageToControllers(store, onGameEnded());
        return result;
      }
      if (stateAfter.player.status === PlayerStatus.RoundEnded) {
        sendMessageToControllers(store, onRoundEnded());
        return result;
      }
      if (stateAfter.player.guess && prevGuess !== stateAfter.player.guess) {
        sendMessageToControllers(
          store,
          onGuessingStarted({ guess: stateAfter.player.guess, previousGuess: stateAfter.player.previousGuess })
        );
      }
      return result;
    }

    if (onSkipCurrentGuess.match(action)) {
      playSound("wrongSound", SOUND_EFFECT_VOLUME);
      const result = next(action);
      store.dispatch(startGuessing());
      return result;
    }
    if (startGuessing.match(action)) {
      const result = next(action);
      const state = store.getState() as RootState;

      if (state.player.status === PlayerStatus.Ended) {
        sendMessageToControllers(store, onGameEnded());
        return result;
      }
      if (state.player.status === PlayerStatus.RoundEnded) {
        sendMessageToControllers(store, onRoundEnded());
        return result;
      }
      if (state.player.guess) {
        sendMessageToControllers(store, onGuessingStarted({ guess: state.player.guess, previousGuess: state.player.previousGuess }));
      }
      return result;
    }

    if (startNewRound.match(action)) {
      const result = next(action);
      const state = store.getState() as RootState;
      if (state.player.round) {
        sendMessageToControllers(store, onRoundStarted(state.player.round));
      }
      return result;
    }

    if (startNewTurn.match(action)) {
      const result = next(action);
      const state = store.getState() as RootState;
      if (state.player.turn) {
        sendMessageToControllers(store, onTurnStarted(state.player.turn));
      }
      return result;
    }

    if (startGame.match(action)) {
      const state = store.getState() as RootState;
      const teamAssignments = state.player.users.map((x, index) => ({
        cid: x.cid,
        team: index % 2,
        instructorOrder: index,
      }));

      const sWords = getQueryString('words');
      const theme = getQueryString('theme');
      action.payload.words = shuffle(sWords ? sWords.split(',') : PREMADE_WORDS.words);
      action.payload.theme = theme ? theme : PREMADE_WORDS.name;
  
      action.payload.teamAssignments = teamAssignments;
      sendMessageToControllers(
        store,
        onGameStarted({ teamAssignments })
      );
      return next(action);
    }
    if (sendMessageFromPlayer.match(action)) {
      sendMessageToControllers(store, action.payload);
      return next(action);
    }

    if (initializeAsync.fulfilled.match(action)) {
      const url = `https://kahoot-stage.it/cometd/${action.payload.pin}/${action.payload.sessionToken}`;

      cometd.configure({
        url,
        logLevel: "debug",
        maxNetworkDelay: 40_000,
      });

      cometd.handshake((response) => {
        if (response.successful) {
          cometd.publish("/service/player", {
            type: "started",
            gameid: action.payload.pin,
            host: "kahoot-stage",
          });
          store.dispatch(setIsLoaded());
        } else {
          store.dispatch(setPlayerError("Could not connect to cometd"));
        }
      });
      console.log('subscribed');
      cometd.subscribe(`/controller/${action.payload.pin}`, (message) => {
        console.log('message', message);
        switch (message.data.type) {
          case "joined":
            store.dispatch(
              userJoined({ cid: message.data.cid, name: message.data.name })
            );
            break;
          default:
            console.log('message', message);
            store.dispatch(JSON.parse(message.data.content));
            break;
        }
      });

      return next(action);
    }

    next(action);
  };
