import { Guess, SUCCESS_THRESHOLD, TeamAssignment, Turn } from "../constants";

export function shuffle(array: string[]) {
  let currentIndex = array.length,
    randomIndex;

  // While there remain elements to shuffle.
  while (currentIndex !== 0) {
    // Pick a remaining element.
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex--;

    // And swap it with the current element.
    [array[currentIndex], array[randomIndex]] = [
      array[randomIndex],
      array[currentIndex],
    ];
  }

  return array;
}

export function numberOfAnswerersInTeam(teamAssignments: TeamAssignment[], team: number | undefined) {
  return teamAssignments
    ? teamAssignments.filter((x) => x.team === team).length -
      1 /* removes instructor */
    : 0;
}

export function getVotingProgress(
  teamAssignments: TeamAssignment[],
  turn: Turn | undefined,
  guess: Guess
) {
  const totalUsersInCurrentTeam = numberOfAnswerersInTeam(teamAssignments, turn?.team);
  const totalVotes = guess?.correctAnswers ?? 0;
  return Math.min(
    100,
    Math.round(
      (totalVotes * 100) / (totalUsersInCurrentTeam * SUCCESS_THRESHOLD)
    )
  );
}

export function playSound(soundId: string, volume: number) {
  const el: HTMLAudioElement | null = document.getElementById(
    soundId
  ) as HTMLAudioElement;
  if (el) {
    el.volume = volume;
    el.play();
  }
}
