import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { Countdown } from "../../../components/Countdown";
import { VoteBar } from "./VoteBar";
import { CANVAS_SIZE, RoundId, SOUND_EFFECT_VOLUME, Team, Timing } from "../../../constants";
import { checkIfEnoughAnswers, selectPlayerGuess, selectPlayerRound, selectPlayerTurn, endTurn, startNewRound } from "../playerSlice";
import { useCallback, useEffect } from "react";
import CanvasDraw from "react-canvas-draw";
import LZString from 'lz-string';
import { playSound } from "../../../app/utils";

const emptyDrawing = {
  lines: [],
  width: CANVAS_SIZE,
  height: CANVAS_SIZE
};

export const PlayerGuessingStarted = () => {
  const round = useAppSelector(selectPlayerRound);
  const turn = useAppSelector(selectPlayerTurn);
  const guess = useAppSelector(selectPlayerGuess);
  
  const d = useAppDispatch();
  const onExpiry = useCallback(() => {
    playSound("gongSound", SOUND_EFFECT_VOLUME);
    d(endTurn());
  }, [d]);

  useEffect(() => {
    const timeout = setTimeout(() => {
      d(checkIfEnoughAnswers());
    }, Timing.SlowPlayerWait);
    return () => {
      clearTimeout(timeout);
    }
  }, [d, guess?.correctWord]);
  return (
    <div className='fullscreen centered'>
      <h1>Team {turn?.team === Team.Blue ? 'BLUE' : 'ORANGE' }</h1>
      <Countdown duration={Timing.GuessingSession} onExpiry={onExpiry} />
      {round?.id === RoundId.Round3 && <div style={{ margin: '1rem 0'}}><CanvasDraw immediateLoading={true} canvasWidth={CANVAS_SIZE} canvasHeight={CANVAS_SIZE} saveData={LZString.decompress(guess?.drawing) ?? JSON.stringify(emptyDrawing)} disabled={true}  /></div>}
      <VoteBar showPreviousResult={true} />
      <p><button onClick={() => d(startNewRound())}>Skip round</button></p>
    </div>
  );
};
