import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { Team } from "../../../constants";
import {
  goToNextTurn,
  selectPlayerRound,
  selectPlayerScores,
  selectPlayerTurn,
} from "../playerSlice";
import { Versus } from "./Versus";

export const PlayerTurnEnded = () => {
  const scores = useAppSelector(selectPlayerScores);
  const round = useAppSelector(selectPlayerRound);
  const turn = useAppSelector(selectPlayerTurn);

  const d = useAppDispatch();
  // useEffect(() => {
  //   setTimeout(() => {
  //     d(goToNextTurn());
  //   }, Timing.TeamResultsShown);
  // });

  return (
    <div className="fullscreen centered">
      <h1>Round {round?.id}</h1>
      {!!turn?.guessedWords.length && <div className="instructions">
        The {turn?.team === Team.Blue ? "BLUE" : "ORANGE"} team guessed
      </div>}
      <div className="card-list">
        {turn?.guessedWords.map((x) => (
          <div key={x} className="card">
            {x}
          </div>
        ))}
      </div>
      <Versus scores={scores} />
      <p><button onClick={() => d(goToNextTurn())}>Next</button></p>
    </div>
  );
};
