import { useEffect, useState } from "react";
import { useAppSelector } from "../../../app/hooks";
import { getVotingProgress } from "../../../app/utils";
import { GuessResult, Team, Timing } from "../../../constants";
import {
  selectPlayerGuess,
  selectPlayerPreviousGuess,
  selectPlayerTeamAssignments,
  selectPlayerTurn,
} from "../playerSlice";
import styles from "./VoteBar.module.css";

export const VoteBar = ({
  showPreviousResult = false,
}: {
  showPreviousResult: boolean;
}) => {
  const turn = useAppSelector(selectPlayerTurn);
  const teamAssignments = useAppSelector(selectPlayerTeamAssignments);
  const guess = useAppSelector(selectPlayerGuess);
  const previousGuess = useAppSelector(selectPlayerPreviousGuess);

  const [isPreviousGuessedWordVisible, setIsPreviousGuessedWordVisible] =
    useState(showPreviousResult && !!previousGuess);

  const hasPreviousGuess = !!previousGuess;
  useEffect(() => {
    setIsPreviousGuessedWordVisible(showPreviousResult && hasPreviousGuess);
  }, [showPreviousResult, hasPreviousGuess, previousGuess]);
  const progress = isPreviousGuessedWordVisible
    ? 100
    : turn && guess
    ? getVotingProgress(teamAssignments ?? [], turn, guess)
    : 0;

  useEffect(() => {
    if (!showPreviousResult || !isPreviousGuessedWordVisible) {
      return undefined;
    }
    const timeout = setTimeout(() => {
      setIsPreviousGuessedWordVisible(false);
    }, Timing.PreviousGuessedWord);
    return () => {
      clearTimeout(timeout);
    };
  }, [showPreviousResult, isPreviousGuessedWordVisible, previousGuess]);
  return (
    <>
      {!isPreviousGuessedWordVisible && (
        <div className={styles.voteBar}>
          <div className={styles.progress} style={{ width: progress + "%" }} />
        </div>
      )}
      {isPreviousGuessedWordVisible && (
        <>
          {previousGuess?.result === GuessResult.Successful && <p className="instructions">
            Team {turn?.team === Team.Blue ? "BLUE" : "ORANGE"} have guessed{" "}
            {previousGuess?.word}!
          </p>}

          {previousGuess?.result === GuessResult.Passed && <p className="instructions">
            {turn?.team === Team.Blue ? "BLUE" : "ORANGE"} instructor have passed
          </p>}

          {previousGuess?.result === GuessResult.Failed && <p className="instructions">
            Team {turn?.team === Team.Blue ? "BLUE" : "ORANGE"} failed to find the word
          </p>}
        </>
      )}
    </>
  );
};
