import { useAppSelector } from "../../../app/hooks";
import { ControllerError } from "./ControllerError";
import { ControllerWaitingForPin } from "./ControllerWaitingForPin";
import { ControllerWaitingForName } from "./ControllerWaitingForName";
import { ControllerStatus, SOUND_EFFECT_VOLUME } from "../../../constants";
import { selectControllerStatus } from "../controllerSlice";
import { ControllerWaitingForGameToStart } from "./ControllerWaitingForGameToStart";
import { ControllerGameStarted } from "./ControllerGameStarted";
import { ControllerRoundStarted } from "./ControllerRoundStarted";
import { ControllerTurnStarted } from "./ControllerTurnStarted";
import { ControllerGuessingStarted } from "./ControllerGuessingStarted";
import { ControllerTurnEnded } from "./ControllerTurnEnded";
import { ControllerGameEnded } from "./ControllerGameEnded";
import { ControllerRoundEnded } from "./ControllerRoundEnded";
import { useEffect } from "react";
import { playSound } from "../../../app/utils";

export const Controller = () => {
  useEffect(() => {
    const callback = (e: MouseEvent) => {
      const target: any = e.target;
      if (target['nodeName'] === 'BUTTON') {
        playSound("playerAnsweredSound", SOUND_EFFECT_VOLUME);
      }
    };
    document.addEventListener('click', callback);
    return () => {
      document.removeEventListener('click', callback);
    }
  }, []);
  const status = useAppSelector(selectControllerStatus);
  switch (status) {
    case ControllerStatus.WaitingForPin:
      return <ControllerWaitingForPin />;
    case ControllerStatus.WaitingForGameToStart:
      return <ControllerWaitingForGameToStart />;
    case ControllerStatus.GameStarted:
      return <ControllerGameStarted />;
    case ControllerStatus.WaitingForName:
      return <ControllerWaitingForName />;
    case ControllerStatus.RoundStarted:
      return <ControllerRoundStarted />;
    case ControllerStatus.TurnStarted:
      return <ControllerTurnStarted />;
    case ControllerStatus.GuessingStarted:
      return <ControllerGuessingStarted />;
    case ControllerStatus.TurnEnded:
      return <ControllerTurnEnded />;
    case ControllerStatus.RoundEnded:
      return <ControllerRoundEnded />;
    case ControllerStatus.GameEnded:
      return <ControllerGameEnded />;
    case ControllerStatus.Error:
      return <ControllerError />;
  }
};
