import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { RoundId } from "../../../constants";
import { selectPlayerRound, startNewTurn } from "../playerSlice";

export const PlayerRoundStarted = () => {
  const round = useAppSelector(selectPlayerRound);

  const d = useAppDispatch();
  // useEffect(() => {
  //   setTimeout(() => {
  //     d(showRoles());
  //   }, Timing.RoundStarted);
  // });
  
  return (
    <div className='fullscreen centered'>
      <h1>Round {round?.id}</h1>
      <div className="instructions">
        Guess as many words as you can. <br />
        {round?.id === RoundId.Round1 && (
          <>
            The instructor will give you clues out loud.
            <br />
          </>
        )}
        {round?.id === RoundId.Round2 && (
          <>
            The instructor can{" "}
            <span className="red" style={{ color: "white" }}>
              only speak ONE WORD
            </span>
            .<br />
          </>
        )}
        {round?.id === RoundId.Round3 && (
          <>
            The instructor{" "}
            <span className="red" style={{ color: "white" }}>
              cannot speak but will DRAW instead
            </span>
            .<br />
          </>
        )}
        {round?.id !== RoundId.Round3 && (
          <>No part or variant of the word can be used in the clue.</>
        )}
      </div>
            <p><button onClick={() => d(startNewTurn())}>Next</button></p>
    </div>
  );
};
