import clsx from "clsx";
import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { RoundId, Team } from "../../../constants";
import {
  selectPlayerRound,
  selectPlayerTurn,
  startGuessing,
} from "../playerSlice";

export const PlayerTurnStarted = () => {
  const round = useAppSelector(selectPlayerRound);
  const turn = useAppSelector(selectPlayerTurn);
  const d = useAppDispatch();
  // useEffect(() => {
  //   setTimeout(() => {
  //     d(startGuessing());
  //   }, Timing.RolesShown);
  // });

  return (
    <div className={clsx("fullscreen centered")}>
      <h1>
        Round {round?.id}
      </h1>
      <div className='instructions'>{turn?.team === Team.Blue ? 'BLUE' : 'ORANGE' } team, listen to <strong>{turn?.instructor?.name}</strong>!</div>
      <div className='versus'>
        <div className={clsx('versus__team',  { selected: turn?.team === Team.Blue })}>Blue</div><div className='versus__versus'>vs</div><div className={clsx('versus__team',  { selected: turn?.team === Team.Red })}>Orange</div>
      </div>
      <p>
        <button onClick={() => d(startGuessing())}>Next</button>
      </p>
    </div>
  );
};
