import { useAppSelector } from "../../../app/hooks";
import { PlayerEnded } from "./PlayerEnded";
import { PlayerError } from "./PlayerError";
import { PlayerLoading } from "./PlayerLoading";
import { PlayerStarted } from "./PlayerStarted";
import { PlayerWaitingForPlayers } from "./PlayerWaitingForPlayers";
import { PlayerStatus } from "../../../constants";
import { selectPlayerStatus } from "../playerSlice";
import { PlayerRoundStarted } from "./PlayerRoundStarted";
import { PlayerTurnStarted } from "./PlayerTurnStarted";
import { PlayerGuessingStarted } from "./PlayerGuessingStarted";
import { PlayerTurnEnded } from "./PlayerTurnEnded";
import { PlayerRoundedEnded } from "./PlayerRoundEnded";
import { playSound } from "../../../app/utils";
import { useEffect } from "react";

export const Player = () => {
  const status = useAppSelector(selectPlayerStatus);
  useEffect(() => {
    const callback = () => {
      playSound('lobbySound', 0.03);
      document.removeEventListener('click', callback);
    };
    document.addEventListener('click', callback);
    return () => {
      document.removeEventListener('click', callback);
    }
  }, []);
  switch (status) {
    case PlayerStatus.Loading:
      return <PlayerLoading />;
    case PlayerStatus.WaitingForPlayers:
      return <PlayerWaitingForPlayers />;
    case PlayerStatus.Started:
      return <PlayerStarted />;
    case PlayerStatus.RoundStarted:
      return <PlayerRoundStarted />;
    case PlayerStatus.TurnStarted:
      return <PlayerTurnStarted />;
    case PlayerStatus.GuessingStarted:
      return <PlayerGuessingStarted />;
    case PlayerStatus.TurnEnded:
      return <PlayerTurnEnded />;
    case PlayerStatus.RoundEnded:
      return <PlayerRoundedEnded />;
    case PlayerStatus.Ended:
      return <PlayerEnded />;
    case PlayerStatus.Error:
      return <PlayerError />;
  }
};
