import { useEffect } from "react";
import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { playSound } from "../../../app/utils";
import {
  selectPlayerRound, selectPlayerScores, startNewRound,
} from "../playerSlice";
import { Versus } from "./Versus";

export const PlayerRoundedEnded = () => {
  const round = useAppSelector(selectPlayerRound);
  const scores = useAppSelector(selectPlayerScores);
  const winner = scores[0] > scores[1] ? 0 : scores[0] < scores[1] ? 1 : null;  
  const d = useAppDispatch();
  useEffect(() => {
    playSound('gongSound', 0.2);
  }, []);
  return (
    <div className="fullscreen centered">
      <div className='instructions'>Round {round?.id}</div>
      <h1>
        {winner === null && <div>Close game!</div>}
        {winner === 0 && <div>BLUE team is ahead!</div>}
        {winner === 1 && <div>ORANGE team is ahead!</div>}
      </h1>
      <Versus scores={scores} />
      <p><button onClick={() => d(startNewRound())}>Next</button></p>
    </div>
  );
};
