import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../../app/store";
import {
  getVotingProgress,
  numberOfAnswerersInTeam,
  playSound,
  shuffle,
} from "../../app/utils";
import {
  FINAL_ROUND,
  Guess,
  GuessResult,
  INCORRECT_ANSWERS,
  PlayerStatus,
  PreviousGuess,
  Round,
  RoundId,
  SOUND_EFFECT_VOLUME,
  SUCCESS_THRESHOLD,
  TeamAssignment,
  Turn,
  User,
} from "../../constants";
import { createGameSession } from "./playerAPI";

export interface PlayerState {
  pin?: string;
  round?: Round;
  scores: number[];
  turn?: Turn;
  users: User[];
  theme: string | undefined;
  status: PlayerStatus;
  words?: string[];
  errorMessage: string | null;
  teamAssignments?: TeamAssignment[];
  guess?: Guess;
  previousGuess?: PreviousGuess | undefined;
}

const initialState: PlayerState = {
  users: [],
  scores: [0, 0],
  status: PlayerStatus.Loading,
  errorMessage: null,
  theme: "",
};

let roundCounter = 0;
let turnCounter = 0;
let queuedAnswers: string[] = [];
let shouldQueueAnswers = true;

export const initializeAsync = createAsyncThunk(
  "player/initialize",
  async () => {
    const response = await createGameSession();
    return response;
  }
);

let wordIndex = 0;
let alreadyGuessedWords: string[] = [];

function findRandomWordsNotAlreadyGuessed(
  allWords: string[],
  wordToIgnore: string,
  count: number
) {
  const allValidGuesses = allWords.filter(
    (x) => !alreadyGuessedWords.includes(x) && x !== wordToIgnore
  );
  const guesses = shuffle(allValidGuesses).splice(0, count);
  // TODO: not enough items? Game over.
  return guesses;
}

function updateTurn(turnCounter: number, state: PlayerState) {
  const team = turnCounter % 2;
  const teamUsers = state.teamAssignments?.filter((x) => x.team === team) ?? [];
  // TODO: here we assume the players are already in instructorOrder but really we should explicitly sort
  const instructor = teamUsers[(turnCounter - 1) % teamUsers.length];
  state.turn = {
    guessedWords: [],
    team,
    instructor: state.users.find((x) => x.cid === instructor.cid),
  };
}

function pushFirstWordToTheEnd(words: string[]) {
  if (words.length) {
    const w = words ?? [];
    const firstWord = w.shift();
    if (firstWord) {
      return [...w, firstWord];
    }
  }
  return null;
}

let savedWords: string[];

function goToNextGuess(state: PlayerState) {
  shouldQueueAnswers = true;
  queuedAnswers = [];

  state.words?.shift();
  const allWords = state.words ?? [];

  state.guess = {
    drawing: "",
    incorrectAnswers: 0,
    correctAnswers: 0,
    correctWord: allWords[wordIndex],
    incorrectWords: findRandomWordsNotAlreadyGuessed(
      allWords,
      allWords[wordIndex],
      INCORRECT_ANSWERS
    ),
  };

  if (state.guess?.incorrectWords.length !== INCORRECT_ANSWERS) {
    state.status =
      roundCounter === FINAL_ROUND
        ? PlayerStatus.Ended
        : PlayerStatus.RoundEnded;
  }
}

function processQueuedAnswers(state: PlayerState) {
  if (!state.guess) {
    return;
  }
  console.log(
    "queuedAnswers",
    queuedAnswers,
    "correctWord",
    state.guess?.correctWord
  );
  for (const answer of queuedAnswers) {
    console.log("answer", answer);

    if (answer === state.guess?.correctWord) {
      console.log("is correct");
      state.guess.correctAnswers++;

      const progress =
        state.round && state.guess
          ? getVotingProgress(
              state.teamAssignments ?? [],
              state.turn,
              state.guess
            )
          : 0;
      // guessed correctly
      if (progress >= 100) {
        playSound("successSound", SOUND_EFFECT_VOLUME);
        alreadyGuessedWords.push(state.guess.correctWord);
        if (state.turn) {
          state.turn.guessedWords.push(state.guess.correctWord);
          state.scores[state.turn.team]++;
        }
        state.previousGuess = {
          word: state.guess.correctWord,
          result: GuessResult.Successful,
        };

        goToNextGuess(state);
        break;
      }
    } else {
      console.log("is NOT correct");
      // incorrect guess
      state.guess.incorrectAnswers++;
      const totalUsersInCurrentTeam = numberOfAnswerersInTeam(
        state.teamAssignments ?? [],
        state.turn?.team
      );
      if (
        state.guess.incorrectAnswers >
        totalUsersInCurrentTeam * (1 - SUCCESS_THRESHOLD)
      ) {
        // Not a great place to put this, but this is a hackathon.
        playSound("wrongSound", SOUND_EFFECT_VOLUME);
        state.previousGuess = {
          word: undefined,
          result: GuessResult.Failed,
        };
        goToNextGuess(state);
        break;
      }
    }
  }
  queuedAnswers = [];
}

export const playerSlice = createSlice({
  name: "player",
  initialState,
  reducers: {
    endTurn: (state) => {
      state.status = PlayerStatus.TurnEnded;
    },
    goToNextTurn: (state) => {
      turnCounter++;
      state.previousGuess = undefined;
      state.status = PlayerStatus.TurnStarted;
      const newArray = pushFirstWordToTheEnd(state.words ?? []);
      if (newArray) {
        state.words = newArray;
      }
      updateTurn(turnCounter, state);
    },
    checkIfEnoughAnswers: (state) => {
      shouldQueueAnswers = false;
      processQueuedAnswers(state);
    },
    onVoteSubmitted: (state, action: PayloadAction<string>) => {
      queuedAnswers.push(action.payload);
      const totalUsersInCurrentTeam = numberOfAnswerersInTeam(
        state.teamAssignments ?? [],
        state.turn?.team
      );

      if (
        !shouldQueueAnswers ||
        queuedAnswers.length === totalUsersInCurrentTeam
      ) {
        // process instantly
        processQueuedAnswers(state);
      }
    },
    onSkipCurrentGuess: (state) => {
      const newArray = pushFirstWordToTheEnd(state.words ?? []);
      if (newArray) {
        state.words = newArray;
      }
      state.previousGuess = {
        word: undefined,
        result: GuessResult.Passed,
      };
    },
    startGuessing: (state) => {
      state.status = PlayerStatus.GuessingStarted;

      const allWords = state.words ?? [];
      state.guess = {
        drawing: "",
        correctAnswers: 0,
        incorrectAnswers: 0,
        correctWord: allWords[wordIndex],
        incorrectWords: findRandomWordsNotAlreadyGuessed(
          allWords,
          allWords[wordIndex],
          INCORRECT_ANSWERS
        ),
      };

      if (state.guess.incorrectWords.length !== INCORRECT_ANSWERS) {
        state.status =
          roundCounter === FINAL_ROUND
            ? PlayerStatus.Ended
            : PlayerStatus.RoundEnded;
      }
    },
    startNewTurn: (state) => {
      state.status = PlayerStatus.TurnStarted;
      state.previousGuess = undefined;
      state.words = shuffle([...savedWords]);
      wordIndex = 0;
      alreadyGuessedWords = [];
      turnCounter++;
      updateTurn(turnCounter, state);
    },
    startNewRound: (state) => {
      state.status = PlayerStatus.RoundStarted;
      roundCounter++;
      state.round = { id: roundCounter };
    },
    userJoined: (state, action: PayloadAction<User>) => {
      if (state.status === PlayerStatus.WaitingForPlayers) {
        state.users.push(action.payload);
      }
    },
    setIsLoaded: (state) => {
      state.status = PlayerStatus.WaitingForPlayers;
    },
    setPlayerError: (state, action: PayloadAction<string>) => {
      state.status = PlayerStatus.Error;
      state.errorMessage = action.payload;
    },
    startGame: (
      state,
      action: PayloadAction<{
        theme?: string;
        words?: string[];
        teamAssignments?: TeamAssignment[];
      }>
    ) => {
      state.status = PlayerStatus.Started;
      savedWords = action.payload.words ?? [];
      state.theme = action.payload.theme;
      state.words = action.payload.words;
      state.teamAssignments = action.payload.teamAssignments;
    },
    sendMessageFromPlayer: (state, action: PayloadAction<string>) => {
      // handled in middleware
    },
    onDrawingUpdated: (state, action: PayloadAction<any>) => {
      if (state.guess) {
        state.guess.drawing = action.payload;
      }
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(initializeAsync.fulfilled, (state, action) => {
        state.pin = action.payload.pin;
      })
      .addCase(initializeAsync.rejected, (state) => {
        state.status = PlayerStatus.Error;
        state.errorMessage = "initializeAsync.rejected";
      });
  },
});

export const selectPlayerStatus = (state: RootState) => state.player.status;
export const selectPlayerTheme = (state: RootState) => state.player.theme;
export const selectPlayerPin = (state: RootState) => state.player.pin;
export const selectPlayerGuess = (state: RootState) => state.player.guess;
export const selectPlayerPreviousGuess = (state: RootState) =>
  state.player.previousGuess;
export const selectPlayerScores = (state: RootState) => state.player.scores;
export const selectPlayerRound = (state: RootState) => state.player.round;
export const selectPlayerTurn = (state: RootState) => state.player.turn;
export const selectPlayerUsers = (state: RootState) => state.player.users;
export const selectPlayerTeamAssignments = (state: RootState) =>
  state.player.teamAssignments;
export const selectPlayerErrorMessage = (state: RootState) =>
  state.player.errorMessage;

export const {
  onDrawingUpdated,
  endTurn,
  goToNextTurn,
  onVoteSubmitted,
  onSkipCurrentGuess,
  setIsLoaded,
  setPlayerError,
  startGame,
  sendMessageFromPlayer,
  userJoined,
  startNewRound,
  startNewTurn,
  startGuessing,
  checkIfEnoughAnswers,
} = playerSlice.actions;

export default playerSlice.reducer;
