import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../../app/store';
import { ControllerStatus, Guess, PreviousGuess, Round, TeamAssignment, Turn } from '../../constants';
import { joinGameSession } from './controllerAPI';

export interface ControllerState {
  status: ControllerStatus;
  clientId?: string | null;
  sessionId?: string;
  errorMessage?: string;
  name?: string;
  pin?: string;
  guess?: Guess;
  round?: Round;
  turn?: Turn;
  previousGuess?: PreviousGuess | undefined;
  teamAssignments?: TeamAssignment[];
}

const initialState: ControllerState = {
  status: ControllerStatus.WaitingForPin,
};

export const joinGameAsync = createAsyncThunk(
  'controller/joinGame',
  async (pin: string) => {
    const response = await joinGameSession(pin);
    return response;
  }
);


export const controllerSlice = createSlice({
  name: 'controller',
  initialState,
  reducers: {
    onRoundEnded: (state) => {
      state.status = ControllerStatus.RoundEnded;
    },
    onGameEnded: (state) => {
      state.status = ControllerStatus.GameEnded;
    },
    onTurnEnded: (state) => {
      state.status = ControllerStatus.TurnEnded;
    },
    onRoundStarted: (state, action: PayloadAction<Round>) => {
      state.status = ControllerStatus.RoundStarted;
      state.round = action.payload;
    },
    skipCurrentGuess: (state) => {
      // dealt in middleware
    },
    onGuessingStarted: (state, action: PayloadAction<{ guess: Guess; previousGuess: PreviousGuess | undefined }>) => {
      if (state.status === ControllerStatus.TurnEnded) {
        return;
      }
      state.status = ControllerStatus.GuessingStarted;
      state.guess = action.payload.guess;
      state.previousGuess = action.payload.previousGuess;
    },
    onTurnStarted: (state, action: PayloadAction<Turn>) => {
      state.status = ControllerStatus.TurnStarted;
      state.turn = action.payload;
    },
    setControllerError: (state, action: PayloadAction<string>) => {
      state.status = ControllerStatus.Error;
      state.errorMessage = action.payload;
    },
    setClientId: (state, action: PayloadAction<string | null>) => {
      state.clientId = action.payload;
    },
    onGameStarted: (state, action: PayloadAction<{ teamAssignments: TeamAssignment[] }>) => {
      state.status = ControllerStatus.GameStarted;
      state.teamAssignments = action.payload.teamAssignments;
    },
    setName: (state, action: PayloadAction<string>) => {
      state.status = ControllerStatus.WaitingForGameToStart;
      state.name = action.payload;
    },
    sendMessageFromController: (state, action: PayloadAction<string>) => {
        // handled in middleware
    },
    updateDrawing: (state, action: PayloadAction<any>) => {
      // handled in middleware
    },
    submitVote: (state, action: PayloadAction<string>) => {

    }
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(joinGameAsync.fulfilled, (state, action) => {
        state.status = ControllerStatus.WaitingForName;
        state.pin = action.payload.pin;
        state.sessionId = action.payload.sessionId;
      })
      .addCase(joinGameAsync.rejected, (state) => {
        state.status = ControllerStatus.Error;
        state.errorMessage = 'joinGameAsync.rejected';
      });
  },
});

export const selectControllerStatus = (state: RootState) => state.controller.status;
export const selectControllerGuess = (state: RootState) => state.controller.guess;
export const selectControllerPreviousGuess = (state: RootState) => state.controller.previousGuess;
export const selectControllerClientId = (state: RootState) => state.controller.clientId;
export const selectControllerRound = (state: RootState) => state.controller.round;
export const selectControllerTurn = (state: RootState) => state.controller.turn;
export const selectControllerTeamAssignments = (state: RootState) => state.controller.teamAssignments;
export const selectControllerErrorMessage = (state: RootState) => state.controller.errorMessage;

export const { updateDrawing, onRoundEnded, onGameEnded, onTurnEnded, onRoundStarted, submitVote, skipCurrentGuess, setControllerError, setName, sendMessageFromController, onGameStarted, setClientId, onTurnStarted, onGuessingStarted } = controllerSlice.actions;

export default controllerSlice.reducer;
